import {createSlice} from "@reduxjs/toolkit";

const initialState = {
  taskCards: [
    {
      id: 1,
      name: '     ,         .',
      selectTask: true,
    },
    {
      id: 2,
      name: '    ,  CSS  ',
      selectTask: false,
    },
  ],
  taskCardsFinish: [
  ],
  isEditTaskBlock: false,
  isCreateTask: false,
};

const Tasks = createSlice({
  name: 'selectTest',
  initialState,
  reducers: {
    setSelectTask (state, action) {
      state.taskCards = state.taskCards.map(card => {
        return {
          ...card,
          selectTask: action.payload.id === card.id ? action.payload.selectTask : card.selectTask,
        };
      });
    },
    setTaskCards (state, action) {
      state.taskCards = action.payload;
    },
    setTaskCardsFinishSel (state, action) {
      state.taskCardsFinish = action.payload;
    },
    addNewTask (state, action) {
      state.taskCards.push({
        id: state.taskCards.length + 1,
        name: action.payload.value
      });
    },
    setIsCreateTask (state, action) {
      state.isCreateTask = action.payload;
    },
    setIsEditTaskBlock (state, action) {
      state.isEditTaskBlock = action.payload;
    },
    deleteTask (state, action) {
      state.taskCards = state.taskCards.filter(el => el.id !== action.payload);
    },
    deleteTaskFinish (state, action) {
      state.taskCardsFinish = state.taskCardsFinish.filter(el => el.id !== action.payload);
    },
    setTaskCardsFinish (state, action) {
      let newObj = {
        id: state.taskCardsFinish.length + 1,
      };
      state.taskCards = state.taskCards.filter(el => {
        if(el.id === action.payload) {
          newObj.name = el.name
        };
        return el.id !== action.payload;
      });
      state.taskCardsFinish.push(newObj)
    },
  },
});

export const {
  deleteTaskFinish,
  setTaskCardsFinishSel,
  setTaskCardsFinish,
  setTaskCards,
  addNewTask,
  setIsCreateTask,
  setIsEditTaskBlock,
  setSelectTask,
  deleteTask
} = Tasks.actions;

export default Tasks.reducer;