import React from 'react';
import styles from './BtnItemReorder.module.css';

export const BtnItemReorder = (props) => {
  return (
    <div
      className={styles.reorderHandle}
      {...props}
    >
      <div className={styles.reorderHandleItem + ' ' + styles.rh1}></div>
      <div className={styles.reorderHandleItem + ' ' + styles.rh2}></div>
      <div className={styles.reorderHandleItem + ' ' + styles.rh3}></div>
    </div>
  );
};