import './CreateTaskBlock.css';
import React, {useCallback, useState} from "react";
import {textareaResize} from "./textareaResize";
import {useDispatch} from "react-redux";
import {addNewTask, setIsCreateTask} from "../../../redux/reducers/tasks";

export const CreateTaskBlock = () => {
  const dispatch = useDispatch();

  const [cache, setCache] = useState(null);
  const [value, setValue] = useState('');

  const onCancel = useCallback(() => dispatch(setIsCreateTask(false)), []);

  const onSetNewTask = () => {
    dispatch(addNewTask({value}));
    dispatch(setIsCreateTask(false))
  };

  return (
    <div className='Create-Task-Block'>
      <div className='Create-Task-Block-wrapper'></div>
      <p className='name-create-task'> </p>
      <textarea
        autoFocus={true}
        rows={2}
        value={value}
        onChange={(e) => setValue(e.target.value)}
        onKeyDown={(e) => textareaResize(e, cache, setCache, 200)}
        placeholder=' '
        className='input-create-task'
      />
      <div className='btn-create-task-block'>
        <ButtonCreateTask
          name=''
          className='btn-create-task-remove'
          callBackClick={onCancel}
        />
        <ButtonCreateTask
          name=''
          className='btn-create-task-add'
          callBackClick={onSetNewTask}
          disabled={value.length === 0}
        />
      </div>
    </div>
  );
};

const ButtonCreateTask = React.memo(({name, className, callBackClick, disabled = false}) => {

  return (
    <button
      onClick={() => callBackClick()}
      className={className}
      disabled={disabled}
    >
      {name}
    </button>
  );
});