import React, {useEffect} from 'react';
import styles from './TodoItem.module.css';
import {BtnItemReorder} from "../BtnItemReorder/BtnItemReorder";
import tasks_ok_icon from "../../../../../foto/task_ok.png";
import tasks_no_icon from "../../../../../foto/task_no.png";
import {useDispatch} from "react-redux";
import {setSelectTask, setTaskCardsFinish} from "../../../../../redux/reducers/tasks";
import {useTouchMoveDelete} from "../../../../../hooks/useTouchDeleteItem";

export const TodoItem = (
  {
    controls,
    item,
    isEditTaskBlock,
    setDeleteBtnSize,
    finish,
  }) => {
  const dispatch = useDispatch();

  const onSetSelectTask = (id, selectTask) => dispatch(setSelectTask({id, selectTask}));

  const [change, deleteBtnSize, setChange] = useTouchMoveDelete(75);

  useEffect(() => {
    setDeleteBtnSize(deleteBtnSize);
  }, [deleteBtnSize]);

  return (
    <button
      className={`${styles.TaskItem} ${finish && styles.TaskItemNone}`}
      onTouchStart={(e) => setChange(e, 'start')}
      onTouchMove={(e) => setChange(e, 'move', change)}
      onTouchEnd={(e) => setChange(e, 'end', change)}
    >
      {isEditTaskBlock && <BtnItemReorder onPointerDown={(e) => controls.start(e)} />}
      {
        !finish && !isEditTaskBlock &&
        <div
          className={styles.BtnTaskFinish}
          onClick={() => dispatch(setTaskCardsFinish(item.id))}
        ></div>
      }
      <p className={`${styles.TaskItemContent}  ${finish && styles.TaskItemContentNone}`}>{item.name}</p>
      {
        isEditTaskBlock &&
          <div
            className={styles.checkboxChooseTask}
            onClick={() => onSetSelectTask(item.id, !item.selectTask)}
          >
            <img src={item.selectTask ? tasks_ok_icon : tasks_no_icon} alt='circle'/>
          </div>
      }
    </button>
  );
};