import './MainBlockHeader.css';
import {MainBlockHeaderItemSelect} from "./MainBlockHeaderItemSelect/MainBlockHeaderItemSelect";
import {MainBlockHeaderItemSettings} from "./MainBlockHeaderItemSettings/MainBlockHeaderItemSettings";
import grey_icon_header_1 from '../../../foto/1_grey_icon_header.png';
import yellow_icon_header_1 from '../../../foto/1_yellow_icon_header.png';
import grey_icon_header_2 from '../../../foto/2_grey__icon_header.png';
import yellow_icon_header_2 from '../../../foto/2_yellow__icon_header.png';
import {useSelector} from "react-redux";

export const MainBlockHeader = ({setIsShow}) => {

  const activeSlide = useSelector((state) => state.homeSwiper.activeSlide);
  const checkActive = num => activeSlide === num;

  return (
    <div className='Main-Block-Header'>
      <div className='Main-Block-Header-center'>
        <MainBlockHeaderItemSelect
          active={checkActive(0)}
          number={0}
          icon_back={grey_icon_header_1}
          icon_fore={yellow_icon_header_1}
        />
        <MainBlockHeaderItemSelect
          active={checkActive(1)}
          number={1}
          icon_back={grey_icon_header_2}
          icon_fore={yellow_icon_header_2}
        />
      </div>
      <MainBlockHeaderItemSettings setIsShow={setIsShow}/>
    </div>
  );
};

