import './HomePage.css';
import {MainBlockHeader} from "./MainBlockHeader/MainBlockHeader";
import {MainBlock} from "./MainBlock/MainBlock";
import {SettingsPage} from "../SettingsPage/SettingsPage";
import {useState} from "react";
import add_icon from '../../foto/add.png'
import {useDispatch, useSelector} from "react-redux";
import {setIsCreateTask} from "../../redux/reducers/tasks";

export const HomePage = () => {
  const dispatch = useDispatch();

  const [isShow, setIsShow] = useState(false);
  const isCreateTask = useSelector(state => state.tasks.isCreateTask);

  const onAddNewTask = () => dispatch(setIsCreateTask(true));

  return (
    <div className='Main-Block border-standard'>
      <MainBlockHeader
        setIsShow={setIsShow}
      />
      <MainBlock />
      <SettingsPage
        isShow={isShow}
        setIsShow={setIsShow}
      />
      {!isCreateTask &&
        <button
          onClick={() => onAddNewTask()}
          className='add-new-block-main'
        >
          <img src={add_icon} alt='add-new-block-main'/>
        </button>
      }
    </div>
  );
};