import './MainBlockHeaderItemSelect.css';
import {useDispatch} from "react-redux";
import {setActiveSlide} from "../../../../redux/reducers/homeSwiper";

export const MainBlockHeaderItemSelect = ({icon_back, icon_fore, active, number}) => {

  const dispatch = useDispatch();

  // const StyleNavLinkIcon = {
  //   opacity: active ? '0.3' : '1',
  //   transform: active ? 'scale(0.9)' : 'scale(1)',
  // };

  const onSetActiveSlideHome = () => dispatch(setActiveSlide(number));

  return (
    <div
      onClick={() => onSetActiveSlideHome()}
      // style={StyleNavLinkIcon}
      className='Main-Block-Header-Item-Select'
    >
      <img
        src={active ? icon_fore : icon_back }
        alt='HeaderIcon'
      />
    </div>
  );
};