import './TodoBlock.css';
import {TodoItemReorderWrapper} from "./CreateTaskBlock/TodoItemReorderWrapper/TodoItemReorderWrapper";
import {Reorder} from "framer-motion";
import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {setTaskCards, setTaskCardsFinishSel} from "../../redux/reducers/tasks";
import {CreateTaskBlock} from "./CreateTaskBlock/CreateTaskBlock";

export const TodoBlock = () => {
  const dispatch = useDispatch();

  const {taskCards, isCreateTask, taskCardsFinish} = useSelector(state => state.tasks);

  const onSetCardPosition = (state) => dispatch(setTaskCards(state));
  const onSetCardFinishPosition = (state) => dispatch(setTaskCardsFinishSel(state))

  return (
    <>
      <Reorder.Group
        axis="y"
        values={taskCards}
        onReorder={onSetCardPosition}
        className='Todo-Block'
      >
        {taskCards.length !== 0 && taskCards.map(item =>
          <TodoItemReorderWrapper
            key={item.id}
            item={item}
            finish={false}
          />
        )}
      </Reorder.Group>
      <div className='block-finish'>
        <p> {taskCardsFinish.length}</p>
      </div>
      <Reorder.Group
        axis="y"
        values={taskCardsFinish}
        onReorder={onSetCardFinishPosition}
        className='Todo-Block'
      >
        {taskCardsFinish.length !== 0 && taskCardsFinish.map(item =>
          <TodoItemReorderWrapper
            key={item.id}
            item={item}
            finish={true}
          />
        )}
      </Reorder.Group>
      {isCreateTask && <CreateTaskBlock/>}
    </>
  )
};

