import './SettingsPage.css';
import {useDispatch} from "react-redux";
import {ThunkSetLogout} from "../../redux/thunks/Auth";
import arrow_icon from '../../foto/arrow.png';

export const SettingsPage = ({isShow, setIsShow}) => {
  const dispatch = useDispatch();

  const onLogOut = () => {
    dispatch(ThunkSetLogout());
  };

  return (
    <div className={`Settings-Page border-standard ${isShow ? 'SettingsPageOn': 'Settings-Page'}`}>
      <button
        onClick={() => setIsShow(false)}
        className='btn-arrows-settings'
      >
        <img src={arrow_icon} className='arrow-icon' alt='arrow' />
      </button>
      <div>
        <button
          className='btn-logout-settings'
          onClick={() => onLogOut()}
        ></button>
      </div>
    </div>
  );
};