import './TodoItemReorderWrapper.module.css';
import {Reorder, useDragControls} from "framer-motion";
import {useDispatch, useSelector} from "react-redux";
import {deleteTask, deleteTaskFinish} from "../../../../redux/reducers/tasks";
import {TodoItem} from "./TodoItem/TodoItem";
import basket from '../../../../foto/basket.png';
import {useState} from "react";
import styles from './TodoItemReorderWrapper.module.css';

export const TodoItemReorderWrapper = ({item, finish}) => {
  const controls = useDragControls();
  const dispatch = useDispatch();

  const isEditTaskBlock = useSelector(state => state.tasks.isEditTaskBlock);
  const [deleteBtnSize, setDeleteBtnSize] = useState(false);

  const onDeleteTask = () => dispatch(finish ? deleteTaskFinish(item.id) : deleteTask(item.id));

  return (
    <Reorder.Item
      key={item.id}
      value={item}
      className={styles.TaskItemWrapper}
      dragListener={finish ? false : isEditTaskBlock}
      dragControls={controls}
    >
      <div
        className={`${styles.btnDeleteItem} ${deleteBtnSize && styles.deleteBtnSizeOff}`}
        onClick={() => onDeleteTask()}
      >
        <img src={basket} alt="basket"/>
      </div>
      <TodoItem
        controls={controls}
        item={item}
        finish={finish}
        isEditTaskBlock={isEditTaskBlock}
        setDeleteBtnSize={setDeleteBtnSize}
      />
    </Reorder.Item>
  );
};



